













import Vue from 'vue'
import { Component, Prop } from 'vue-property-decorator'

@Component
export default class Confirm extends Vue {
  @Prop({type: String}) message!: string
  @Prop({type: Boolean}) isShow!: boolean

  isEnsure = false

  cancel() {
    this.isEnsure = false
    this.$emit('update:value', this.isEnsure)
  }

  ensure() {
    this.isEnsure = true
    this.$emit('update:value', this.isEnsure)
  }

}

